<template>
  <div>
    <div class="head-background" ref="headBackground">
      <strong style="font-size: 50px">航班</strong>
    </div>
    <div v-loading="loading" style="overflow: auto; height: 900px">
      <el-row type="flex" justify="center" class="search-nav">
        <el-col :xs="20" :sm="18" :md="16" :lg="12" :xl="12">
          <el-card
            shadow="always"
            class="search-nav-el-card"
            :body-style="{ padding: 0 }"
            style="position: relative; overflow: visible"
          >
            <el-row
              type="flex"
              justify="space-around"
              align="middle"
              style="height: 100px"
            >
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.from"
                  placeholder="出发地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                  style="outline: none; border: none; box-shadow: none"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-cascader
                  v-model="search.to"
                  placeholder="目的地"
                  :options="options"
                  filterable
                  :props="search.pro"
                  :clearable="true"
                  :show-all-levels="false"
                ></el-cascader>
              </el-col>
              <el-col :xs="8" :sm="8" :md="5" :lg="5" :xl="5">
                <el-date-picker
                  v-model="search.date"
                  type="date"
                  placeholder="选择日期"
                  editable
                  style="width: 100%"
                  :clearable="true"
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </el-col>
            </el-row>
            <el-button
              icon="el-icon-search"
              round
              @click="searchFilter"
              type="primary"
              style="
                position: absolute;
                bottom: -20px;
                left: 50%;
                z-index: 2;
                width: 150px;
                transform: translateX(-75px);
              "
              >搜索</el-button
            >
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" style="margin: 20px 0" v-if="isShow">
        <el-col :xs="20" :sm="18" :md="16" :lg="14" :xl="14">
          <el-card
            shadow="always"
            class="filter-el-card"
            :body-style="{ padding: '5px' }"
          >
            <div
              style="
                display: flex;
                justify-content: end;
                align-items: center;
                height: 100%;
              "
            >
              <el-select
                v-model="listQuery.price"
                placeholder="价格"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in priceOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.time"
                placeholder="耗时"
                style="width: 120px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in timeOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.depart"
                placeholder="起飞时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in departOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-select
                v-model="listQuery.arrive"
                placeholder="到达时间"
                style="width: 140px; margin: 0 10px"
                :clearable="true"
              >
                <el-option
                  v-for="(item, index) in arriveOptions"
                  :key="index"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <el-button
                :circle="true"
                slot="reference"
                class="el-icon-search"
                @click="handlerFilter"
              ></el-button>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <el-row type="flex" justify="center" class="nav" v-if="isShow">
        <el-col
          :xs="20"
          :sm="18"
          :md="16"
          :lg="14"
          :xl="14"
          v-infinite-scroll="loadLimit"
        >
          <div v-for="(plane, index) in planesPart" :key="index">
            <el-card
              shadow="always"
              class="info-el-card"
              :body-style="{ padding: 0 }"
            >
              <el-row
                type="flex"
                justify="space-between"
                align="middle"
                style="height: 120px"
              >
                <el-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
                  <div class="row">
                    <el-image
                      :src="
                        require('@/assets/png/' +
                          plane.info[0].flightId.slice(0, 2) +
                          '.png')
                      "
                      style="
                        margin-right: 10px;
                        max-height: 50px;
                        max-width: 50px;
                      "
                    ></el-image>
                    <div class="col">
                      <h3>
                        {{
                          "#" +
                          planeDir[plane.info[0].flightId.slice(0, 2)] +
                          "#"
                        }}
                      </h3>
                      <div>
                        {{ plane.info[0].flightId }} /
                        {{ plane.info[0].aircraftType }}
                      </div>
                    </div>
                  </div>
                </el-col>
                <el-col :xs="10" :sm="8" :md="10" :lg="10" :xl="10">
                  <el-popover
                    placement="top"
                    title="航班信息"
                    width="300"
                    trigger="hover"
                  >
                    <div>航班号：{{ plane.info[0].flightId }}</div>
                    <div>飞机型号：{{ plane.info[0].aircraftType }}</div>
                    <el-divider></el-divider>
                    <div>
                      <el-timeline
                        v-for="(item, index) in plane.info"
                        :key="index"
                      >
                        <el-timeline-item
                          :timestamp="
                            item.flightStartTime.toString().slice(0, 5) +
                            ' ' +
                            item.flightStartCity +
                            ' ' +
                            item.flightStartPort +
                            '机场'
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                          <el-card
                            >飞行时长{{
                              timeFilter(
                                item.flightStartTime,
                                item.flightEndTime
                              )
                            }}</el-card
                          >
                        </el-timeline-item>
                        <el-timeline-item
                          :timestamp="
                            item.flightEndTime.toString().slice(0, 5) +
                            ' ' +
                            item.flightEndCity +
                            ' ' +
                            item.flightEndPort +
                            '机场'
                          "
                          placement="top"
                          color="#54c8f4"
                        >
                        </el-timeline-item>
                      </el-timeline>
                    </div>
                    <div class="row" slot="reference">
                      <div class="col">
                        <h3>
                          {{
                            plane.info[0].flightStartCity +
                            plane.info[0].flightStartPort
                          }}
                        </h3>
                        <div>
                          {{
                            plane.info[0].flightStartTime.toString().slice(0, 5)
                          }}
                        </div>
                      </div>
                      <div class="col">
                        <div
                          v-if="plane.info.length > 1"
                          style="
                            transform: translate(0, 10px);
                            color: #54c8f4;
                            font-size: 18px;
                            font-weight: bold;
                          "
                        >
                          中转/经停
                        </div>
                        <el-image
                          :src="require('@/assets/arrow.png')"
                          style="margin: 0 10px; width: 80px; height: 30px"
                        >
                        </el-image>
                      </div>
                      <div class="col">
                        <h3>
                          {{
                            plane.info[plane.info.length - 1].flightEndCity +
                            plane.info[plane.info.length - 1].flightEndPort
                          }}
                        </h3>
                        <div class="row">
                          <strong v-if="compareTime(plane.info)">次日</strong>
                          <div>
                            {{
                              plane.info[plane.info.length - 1].flightEndTime
                                .toString()
                                .slice(0, 5)
                            }}
                          </div>
                        </div>
                      </div>
                    </div>
                  </el-popover>
                </el-col>
                <el-col :xs="5" :sm="3" :md="3" :lg="3" :xl="3">
                  <h3 class="price" style="color: #409eff; font-size: 25px">
                    {{
                      plane.info.length > 1
                        ? plane.info[1].flightLPrice +
                          plane.info[0].flightLPrice
                        : plane.info[0].flightLPrice
                    }}
                    ￥
                  </h3>
                </el-col>
                <el-col :xs="4" :sm="3" :md="3" :lg="3" :xl="3">
                  <el-button
                    type="primary"
                    size="medium"
                    class="el-icon-arrow-down"
                    @click="
                      plane.show = !plane.show;
                      showType(index);
                    "
                    >订购</el-button
                  >
                </el-col>
              </el-row>
            </el-card>
            <el-card v-if="plane.show">
              <div class="col">
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                >
                  <el-tag type="success" v-if="plane.seat.lseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="plane.seat.lseat < 10"
                    >仅剩 {{ plane.seat.lseat }} 张</el-tag
                  >
                  <div class="col" style="margin-left: 20px">
                    <div style="color: #409eff">经济舱</div>
                    <div style="color: #409eff">
                      ￥{{
                        plane.info.length > 1
                          ? plane.info[1].flightLPrice +
                            plane.info[0].flightLPrice
                          : plane.info[0].flightLPrice
                      }}
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(plane, 1)"
                    v-if="plane.seat.lseat > 0"
                  >
                    预定
                  </button>
                </div>
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                >
                  <el-tag type="success" v-if="plane.seat.mseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="plane.seat.mseat < 10"
                    >仅剩 {{ plane.seat.mseat }} 张</el-tag
                  >
                  <div class="col" style="margin-left: 20px">
                    <div style="color: #409eff">商务舱</div>
                    <div style="color: #409eff">
                      ￥{{
                        plane.info.length > 1
                          ? plane.info[1].flightMPrice +
                            plane.info[0].flightMPrice
                          : plane.info[0].flightMPrice
                      }}
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(plane, 2)"
                    v-if="plane.seat.mseat > 0"
                  >
                    预定
                  </button>
                </div>
                <div
                  class="row"
                  style="width: 100%; justify-content: end; margin: 10px 0"
                >
                  <el-tag type="success" v-if="plane.seat.hseat >= 10"
                    >余票充足</el-tag
                  >
                  <el-tag type="warning" v-if="plane.seat.hseat < 10"
                    >仅剩 {{ plane.seat.hseat }} 张</el-tag
                  >
                  <div class="col" style="margin-left: 20px">
                    <div style="color: #409eff">头等舱</div>
                    <div style="color: #409eff">
                      ￥{{
                        plane.info.length > 1
                          ? plane.info[1].flightTPrice +
                            plane.info[0].flightTPrice
                          : plane.info[0].flightTPrice
                      }}
                    </div>
                  </div>
                  <button
                    class="payButton"
                    style="margin-left: 20px"
                    @click="order(plane, 3)"
                    v-if="plane.seat.hseat > 0"
                  >
                    预定
                  </button>
                </div>
              </div>
            </el-card>
          </div>
        </el-col>
      </el-row>
      <div
        style="
          display: flex;
          justify-content: center;
          align-items: center;
          position: fixed;
          left: 50%;
          bottom: 20px;
        "
      >
        <div
          style="height: 40px; width: 40px"
          v-loading="isInfiniteLoading"
        ></div>
      </div>
      <div class="loading" v-if="cardLoading"></div>
    </div>

    <el-dialog
      title="订单"
      :visible.sync="dialog"
      append-to-body
      fullscreen
      width="700px"
      customClass="customWidth"
      @open="initOrder"
    >
      <div class="col">
        <el-steps
          :active="active"
          style="width: 900px; background: #fff"
          align-center
        >
          <el-step title="乘机信息"></el-step>
          <el-step title="增值服务"></el-step>
          <el-step title="支付"></el-step>
        </el-steps>
        <el-divider></el-divider>
        <div style="width: 100%; height: 700px" v-if="active == 0">
          <div class="row">
            <div class="col">
              <el-card
                style="width: 800px; height: 550px; overflow-y: auto"
                header="乘机人"
              >
                <div class="col">
                  <el-card
                    v-for="(passenger, index) in orderInfo.passengers"
                    :key="index"
                    style="
                      width: 100%;
                      height: 150px;
                      margin-bottom: 20px;
                      position: relative;
                    "
                    body-style="padding: 0"
                    shadow="hover"
                  >
                    <div
                      class="row"
                      style="justify-content: start; align-items: start"
                    >
                      <h1 style="margin: 10px 30px; color: #409eff">
                        {{ index }}
                      </h1>
                      <div
                        style="
                          width: 2px;
                          height: 140px;
                          background: #eee;
                          margin: 5px 10px;
                        "
                      ></div>
                      <el-form
                        :model="passenger"
                        label-width="100px"
                        :rules="rules"
                        ref="passengerForm"
                        style="margin-top: 10px"
                      >
                        <el-form-item
                          label="姓名"
                          size="mini"
                          prop="passengerName"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘机人姓名"
                            v-model="passenger.passengerName"
                          ></el-input>
                        </el-form-item>
                        <el-form-item
                          label="身份证"
                          size="mini"
                          prop="passengerId"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘机人身份证号"
                            v-model="passenger.passengerId"
                          ></el-input>
                        </el-form-item>
                        <el-form-item
                          label="联系电话"
                          size="mini"
                          prop="phoneNumber"
                        >
                          <el-input
                            size="mini"
                            placeholder="乘机人电话号码"
                            v-model="passenger.phoneNumber"
                          ></el-input>
                        </el-form-item>
                      </el-form>
                      <el-button
                        class="el-icon-close"
                        type="text"
                        style="
                          font-size: 30px;
                          color: #409eff;
                          position: absolute;
                          right: 10px;
                          top: 0;
                        "
                        @click="deletePassenger(index)"
                      ></el-button>
                    </div>
                  </el-card>
                  <el-button
                    class="el-icon-circle-plus-outline"
                    type="text"
                    style="font-size: 20px; color: #409eff"
                    @click="addPassenger"
                    >增加乘机人</el-button
                  >
                </div>
              </el-card>
              <el-card
                style="width: 800px; height: 120px; margin-top: 20px"
                header="联系人"
              >
                <div class="col">
                  <el-form
                    :model="orderInfo.contact"
                    label-width="100px"
                    ref="contactForm"
                    :rules="contactRules"
                  >
                    <el-form-item label="联系电话" size="mini" prop="phone">
                      <el-input
                        size="mini"
                        placeholder="联系人电话号码"
                        style="width: 300px; margin-right: 20px"
                        v-model="orderInfo.contact.phone"
                      ></el-input>
                      <div class="el-icon-warning" style="color: #7c7c7c">
                        订单信息将发送到此手机号
                      </div>
                    </el-form-item>
                  </el-form>
                </div>
              </el-card>
            </div>
            <el-card
              style="width: 400px; height: 700px; margin-left: 20px"
              header="信息"
            >
              <div class="col">
                <h2>
                  {{ search.date + " " + bookPlane.from + "-" + bookPlane.to }}
                </h2>
                <h2>{{ bookPlane.planeCompany }}</h2>
                <el-divider></el-divider>
                <div
                  v-for="(item, index) in orderInfo.passengers"
                  :key="index"
                  style="width: 100%; margin-bottom: 10px"
                >
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>{{ bookPlane.planeType }}</div>
                      <div>{{ bookPlane.price }}￥</div>
                    </div>
                  </div>
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>机场建设费</div>
                      <div>100￥</div>
                    </div>
                  </div>
                  <div class="col">
                    <div
                      class="row"
                      style="width: 100%; justify-content: space-between"
                    >
                      <div>燃油费</div>
                      <div>150￥</div>
                    </div>
                  </div>
                </div>
                <el-divider></el-divider>
                <h1
                  style="
                    width: 100%;
                    text-align: right;
                    font-size: 20px;
                    color: #409eff;
                  "
                >
                  {{ (bookPlane.price + 250) * orderInfo.passengers.length }}￥
                </h1>
              </div>
            </el-card>
          </div>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 1">
          <el-alert
            title="增值服务为自助选择业务，不影响您的航班预订，所有增值服务解释权归航空公司所有。"
            type="warning"
            show-icon
            style="margin-bottom: 20px"
          >
          </el-alert>
          <el-card style="width: 100%; height: 640px" header="增值服务">
            <el-empty description="暂无可用增值服务"></el-empty>
          </el-card>
        </div>
        <div style="width: 100%; height: 700px" v-if="active == 2">
          <el-card
            style="width: 100%; height: 700px; position: relative"
            header="支付"
          >
            <div
              style="display:flex:justify-content:start;align-items:center;flex-direction:column"
            >
              <div style="margin-bottom: 20px; font-size: 18px">支付方式</div>
              <div
                class="display: flex; justify-content: start; align-items: center;"
              >
                <div style="margin-top: 40px; font-size: 16px">移动支付</div>
                <el-divider></el-divider>
                <el-radio-group v-model="paySelect">
                  <el-radio label="支付宝">支付宝</el-radio>
                  <el-radio label="微信">微信</el-radio>
                  <el-radio label="银联">银联</el-radio>
                </el-radio-group>
                <div style="margin-top: 40px; font-size: 16px">银行卡支付</div>
                <el-divider></el-divider>
                <el-radio-group v-model="paySelect">
                  <el-radio label="中国银行">中国银行</el-radio>
                  <el-radio label="工商银行">工商银行</el-radio>
                  <el-radio label="建设银行">建设银行</el-radio>
                  <el-radio label="农业银行">农业银行</el-radio>
                  <el-radio label="交通银行">交通银行</el-radio>
                </el-radio-group>
              </div>
            </div>
            <div
              class="el-icon-warning"
              style="
                color: #9a9b9c;
                position: absolute;
                bottom: 20px;
                right: 20px;
              "
            >
              订单生效起20分钟内可支付，逾期将自动取消
            </div>
            <div :v-show="false" v-html="alipay" ref="alipay"></div>
          </el-card>
          <div
            class="row"
            style="
              width: 100%;
              height: 40px;
              justify-content: end;
              margin-top: 20px;
            "
          >
            <h1 style="color: red">总计：</h1>
            <div
              style="
                font-size: 20px;
                font-weight: bold;
                color: #fc9867;
                margin-right: 20px;
              "
            >
              {{ (bookPlane.price + 250) * orderInfo.passengers.length }}￥
            </div>
            <button @click="pay" class="payButton">立即支付</button>
          </div>
        </div>
        <div class="row" style="margin-top: 10px">
          <el-button
            type="primary"
            @click="last"
            v-if="active > 0 && active < 2"
            style="width: 300px; margin-right: 20px"
            >上一步</el-button
          >
          <el-button
            type="primary"
            @click="next"
            v-if="active < 2"
            style="width: 300px"
            >下一步</el-button
          >
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import constData from "../data/data";
import data from "@/data/data";
import { mapMutations } from "vuex";
import axios from "axios";
import webconfig from "@/data/config";

export default {
  name: "YPlane",
  data() {
    return {
      limit: 10,
      loading: true,
      cardLoading: false,
      isInfiniteLoading: false,
      options: constData.planeOptions,
      isShow: false,
      planeDir: data.planeInfo,
      planes: [],
      planesPart: [],
      bookPlane: {
        from: "",
        to: "",
        flightId: "",
        date: "",
        plane: "",
        price: "",
        planeType: "",
      },
      search: {
        from: "",
        startTime: "",
        to: "",
        endTime: "",
        date: "",
        pro: {
          expandTrigger: "hover",
        },
      },
      timeOptions: ["耗时升序", "耗时降序"],
      priceOptions: ["价格升序", "价格降序"],
      arriveOptions: ["到达时间升序", "到达时间降序"],
      departOptions: ["出发时间升序", "出发时间降序"],
      listQuery: {
        time: "",
        price: "",
        arrive: "",
        depart: "",
      },
      rules: {
        passengerName: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        passengerId: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max: 18, message: "长度为 18 个字符", trigger: "blur" },
          {
            pattern: /(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
            message: "请输入正确的身份证号码",
            trigger: "blur",
          },
        ],
        phoneNumber: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      contactRules: {
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "长度为 11 个字符", trigger: "blur" },
        ],
      },
      dialog: false,
      active: 0,
      orderInfo: {
        passengers: [
          {
            name: "",
            id: "",
            phone: "",
          },
        ],
        contact: {
          phone: "",
        },
      },
      alipay: "",
      paySelect: "",
    };
  },
  methods: {
    ...mapMutations("login", {
      changeLoginDialog: "changeLoginDialog",
    }),
    getPassegers() {
      axios({
        url: `${webconfig.url}/passenger/findPsgByUsrId`,
        headers: { token: localStorage.getItem("token") },
        params: { usrId: this.$store.state.user.userForm.usrId },
      })
        .then((value) => {
          if (value.data.code == 200) {
            this.orderInfo.passengers = value.data.data;
            this.orderInfo.passengers.filter((item) => {
              return item.passengerType == "飞机";
            });
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
    },
    loadLimit() {
      console.log("loadLimit");
      this.limit += 5;
      this.planesPart = this.planes.slice(0, this.limit);
      this.isInfiniteLoading = true;
      setTimeout(() => {
        this.isInfiniteLoading = false;
      }, 500);
    },
    searchFilter() {
      if (this.search.from == "" || this.search.to == "") {
        this.$message({
          message: "出发地和目的地不能为空",
          type: "error",
          duration: 1000,
        });
        return;
      }
      if (this.search.from[2] == this.search.to[2]) {
        this.$message({
          message: "出发地和目的地不能相同",
          type: "error",
          duration: 1000,
        });
        return;
      }
      if (this.search.date == "") {
        this.$message({
          message: "出发日期不能为空",
          type: "error",
          duration: 1000,
        });
        return;
      }
      this.$refs.headBackground.style.height = "0px";
      this.cardLoading = true;
      this.isShow = false;
      axios({
        url: `${webconfig.url}/aircraft/query-flight`,
        method: "post",
        data: {
          startCityOrStation: this.search.from[2],
          endCityOrStation: this.search.to[2],
          leaveYearMonthDay: this.search.date,
        },
      })
        .then((value) => {
          if (value.data.code == 200) {
            this.planes = value.data.data;
            this.newPlanes = [];
            this.planes.forEach((item) => {
              this.newPlanes.push({
                info: item,
                show: false,
              });
            });
            this.planes = this.newPlanes;
            console.log(this.planes);
            this.planesPart = this.planes.slice(0, this.limit);
            setTimeout(() => {
              this.cardLoading = false;
              this.isShow = true;
            }, 800);
          } else {
            this.$message({
              message: value.data.message,
              type: "error",
              duration: 1000,
            });
          }
        })
        .catch((error) => {
          this.$message({
            message: error,
            type: "error",
            duration: 1000,
          });
        });
      if (this.cardLoading == true) {
        setTimeout(() => {
          this.cardLoading = false;
        }, 800);
      }
    },
    handlerFilter() {
      if (this.listQuery.price != "") {
        if (this.listQuery.price == "价格升序") {
          this.planes.sort((a, b) => {
            return a.info[0].flightLPrice - b.info[0].flightLPrice;
          });
        } else if (this.listQuery.price == "价格降序") {
          this.planes.sort((a, b) => {
            return b.info[0].flightLPrice - a.info[0].flightLPrice;
          });
        }
      }
      if (this.listQuery.time != "") {
        if (this.listQuery.time == "耗时升序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a.info[a.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a.info[0].flightStartTime.toString().slice(0, 5)}`
              ) -
              (Date.parse(
                `1900-01-01 ${b.info[b.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
                Date.parse(
                  `1900-01-01 ${b.info[0].flightStartTime
                    .toString()
                    .slice(0, 5)}`
                ))
            );
          });
        } else if (this.listQuery.time == "耗时降序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b.info[b.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b.info[0].flightStartTime.toString().slice(0, 5)}`
              ) -
              (Date.parse(
                `1900-01-01 ${a.info[a.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
                Date.parse(
                  `1900-01-01 ${a.info[0].flightStartTime
                    .toString()
                    .slice(0, 5)}`
                ))
            );
          });
        }
      }
      if (this.listQuery.arrive != "") {
        if (this.listQuery.arrive == "到达时间升序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a.info[a.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b.info[b.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              )
            );
          });
        } else if (this.listQuery.arrive == "到达时间降序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b.info[b.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a.info[a.info.length - 1].flightEndTime
                  .toString()
                  .slice(0, 5)}`
              )
            );
          });
        }
      }
      if (this.listQuery.depart != "") {
        if (this.listQuery.depart == "出发时间升序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${a.info[0].flightStartTime.toString().slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${b.info[0].flightStartTime.toString().slice(0, 5)}`
              )
            );
          });
        } else if (this.listQuery.depart == "出发时间降序") {
          this.planes.sort((a, b) => {
            return (
              Date.parse(
                `1900-01-01 ${b.info[0].flightStartTime.toString().slice(0, 5)}`
              ) -
              Date.parse(
                `1900-01-01 ${a.info[0].flightStartTime.toString().slice(0, 5)}`
              )
            );
          });
        }
      }
      this.planesPart = this.planes.slice(0, this.limit);
    },
    order(row, type) {
      if (this.$store.state.login.loginState == false) {
        this.changeLoginDialog();
        this.$message({
          message: "请先登录",
          type: "warning",
        });
      } else {
        this.active = 0;
        this.dialog = true;
        this.getPassegers();
        this.bookPlane.from = row.info[0].flightStartCity;
        this.bookPlane.startTime = row.info[0].flightStartTime;
        this.bookPlane.to = row.info[row.info.length - 1].flightEndCity;
        this.bookPlane.endTime = row.info[row.info.length - 1].flightEndTime;
        this.bookPlane.flightId = row.info[0].flightId;
        this.bookPlane.date = this.search.date;
        this.bookPlane.planeCompany =
          this.planeDir[row.info[0].flightId.slice(0, 2)];
        if (row.info.length == 1) {
          switch (type) {
            case 1:
              this.bookPlane.price = row.info[0].flightLPrice;
              this.bookPlane.planeType = "经济舱";
              break;
            case 2:
              this.bookPlane.price = row.info[0].flightMPrice;
              this.bookPlane.planeType = "商务舱";
              break;
            case 3:
              this.bookPlane.price = row.info[0].flightTPrice;
              this.bookPlane.planeType = "头等舱";
              break;
          }
        } else {
          switch (type) {
            case 1:
              this.bookPlane.price =
                row.info[0].flightLPrice + row.info[1].flightLPrice;
              this.bookPlane.planeType = "经济舱";
              break;
            case 2:
              this.bookPlane.price =
                row.info[0].flightMPrice + row.info[1].flightMPrice;
              this.bookPlane.planeType = "商务舱";
              break;
            case 3:
              this.bookPlane.price =
                row.info[0].flightTPrice + row.info[1].flightTPrice;
              this.bookPlane.planeType = "头等舱";
              break;
          }
        }
      }
    },
    next() {
      if (this.active < 3) {
        if (this.active == 0) {
          if (this.orderInfo.passengers.length == 0) {
            this.$message({
              message: "请添加乘客",
              type: "warning",
            });
            return;
          }
          let judge = true;
          console.log(this.$refs);
          this.$refs.passengerForm.forEach((item) => {
            console.log(item);
            item.validate((valid) => {
              if (!valid) {
                judge = false;
              }
            });
          });
          if (!judge) {
            this.$message({
              message: "请填写正确的乘客信息",
              type: "warning",
            });
            return;
          } else {
            this.$refs.contactForm.validate((valid) => {
              if (valid) {
                this.addPassengers();
                this.active++;
              } else {
                this.$message({
                  message: "请填写联系人信息",
                  type: "warning",
                });
              }
            });
          }
        } else if (this.active == 1) {
          this.active++;
        } else if (this.active == 2) {
          this.active++;
        }
      }
    },
    last() {
      if (this.active > 0) {
        this.active--;
      }
    },
    initOrder() {
      this.orderInfo = {
        passengers: [],
        contact: {
          phone: "",
        },
      };
    },
    addPassenger() {
      let passenger = {
        name: "",
        id: "",
        phone: "",
      };
      this.orderInfo.passengers.push(passenger);
    },
    addPassengers() {
      axios({
        method: "post",
        url: `${webconfig.url}/passenger/addPassengerList`,
        headers: { token: localStorage.getItem("token") },
        data: this.orderInfo.passengers,
      });
    },
    deletePassenger(index) {
      this.orderInfo.passengers.splice(index, 1);
    },
    pay() {
      if (this.paySelect == "支付宝") {
        const data = {
          vehicleType: "飞机",
          flightOrTrainNO: this.bookPlane.flightId,
          startPortOrStation: this.bookPlane.from,
          endPortOrStation: this.bookPlane.to,
          startTime:
            this.search.date +
            " " +
            this.bookPlane.startTime.toString().slice(0, 5),
          endTime:
            this.search.date +
            " " +
            this.bookPlane.endTime.toString().slice(0, 5),
          passengerNames: this.orderInfo.passengers.map((item) => {
            return item.passengerName;
          }),
          seatType: this.bookPlane.planeType,
          money:
            (this.bookPlane.price + 250) * this.orderInfo.passengers.length,
          usrId: this.$store.state.user.userForm.usrId,
        };
        axios({
          method: "post",
          url: `${webconfig.url}/usr/alipay`,
          data: data,
        })
          .then((res) => {
            this.alipay = res.data.data;
            console.log(this.$refs.alipay);
            this.$nextTick(() => {
              this.$refs.alipay.children[0].submit();
            });
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message({
          message: "暂不支持改支付方式",
          type: "warning",
        });
      }
    },
    timeFilter(from, to) {
      let startTime = Date.parse(`1900-01-01 ${from.toString().slice(0, 5)}`);
      let endTime = Date.parse(`1900-01-01 ${to.toString().slice(0, 5)}`);
      let diff = endTime - startTime;

      let hours = Math.floor(diff / 3600000);
      let minutes = Math.floor((diff - hours * 3600000) / 60000);
      let seconds = Math.floor(
        (diff - hours * 3600000 - minutes * 60000) / 1000
      );

      return `${hours.toString().padStart(2, 0)}:${minutes
        .toString()
        .padStart(2, 0)}:${seconds.toString().padStart(2, 0)}`;
    },
    compareTime(info) {
      console.log(info);
      if (info.length > 1) {
        let startTime = Date.parse(
          `1900-01-01 ${info[0].flightStartTime.toString().slice(0, 5)}`
        );
        let endTime = Date.parse(
          `1900-01-01 ${info[1].flightEndTime.toString().slice(0, 5)}`
        );
        if (startTime > endTime) {
          return true;
        }
      } else {
        let startTime = Date.parse(
          `1900-01-01 ${info[0].flightStartTime.toString().slice(0, 5)}`
        );
        let endTime = Date.parse(
          `1900-01-01 ${info[0].flightEndTime.toString().slice(0, 5)}`
        );
        if (startTime > endTime) {
          return true;
        }
      }
    },
    showType(index) {
      let stationOrder = 0;
      this.planes[index].info.forEach((item) => {
        stationOrder *= 10;
        stationOrder += item.flightOrder;
      });
      console.log(stationOrder);
      axios({
        method: "get",
        url: `${webconfig.url}/aircraft/query-aircraft-seat`,
        headers: { token: localStorage.getItem("token") },
        params: {
          flightNo: this.planes[index].info[0].flightId,
          stationOrder: stationOrder,
          date: this.search.date,
        },
      }).then((res) => {
        let seat = res.data.data;
        let lseat = seat.filter((item) => item.seatType == "经济舱");
        let mseat = seat.filter((item) => item.seatType == "商务舱");
        let hseat = seat.filter((item) => item.seatType == "头等舱");
        this.$nextTick(() => {
          this.planes[index] = {
            ...this.planes[index],
            seat: {
              lseat: lseat.length,
              mseat: mseat.length,
              hseat: hseat.length,
            },
          };
          this.planesPart = this.planes.slice(0, this.limit);
        });
      });
    },
  },
  activated() {
    // this.planesPart = this.planes.slice(0, this.limit);
    setTimeout(() => {
      this.$refs.headBackground.style.height = "400px";
      this.loading = false;
    }, 500);
  },
  deactivated() {
    this.loading = true;
    this.isShow = false;
  },
};
</script>

<style scoped>
.search-nav {
  margin: 30px 0;
}
.search-nav-el-card {
  height: 100px;
  border-radius: 10px;
}
.search-nav-el-card:hover {
  box-shadow: 0 2px 15px 1px rgba(0, 0, 0, 0.2);
}
